 project has curated on Interaction and Adobe XD Gallery on Behance. I used Adobe Photoshop, Adobe Illustrator and Adobe Experience Design to create this project. I also made some animated GIF images to explain our application's interaction, and I used ProtoPie to create these micro interactions.")]),_c('lightbox',{staticClass:"section__image",attrs:{"thumbnail":"/src/assets/insharior/dribbble.gif","images":['/src/assets/insharior/dribbble.gif']}}),_c('lightbox',{staticClass:"section__image",attrs:{"thumbnail":"/src/assets/insharior/protopie.png","images":['/src/assets/insharior/protopie.png']}}),_c('lightbox',{staticClass:"section__image no-radius",attrs:{"thumbnail":"/src/assets/insharior/row1.png","images":['/src/assets/insharior/row1.png']}}),_c('lightbox',{staticClass:"section__image no-radius",attrs:{"thumbnail":"/src/assets/insharior/row2.png","images":['/src/assets/insharior/row2.png']}}),_c('lightbox',{staticClass:"section__image no-radius",attrs:{"thumbnail":"/src/assets/insharior/row3.png","images":['/src/assets/insharior/row3.png']}}),_c('lightbox',{staticClass:"section__image no-radius",attrs:{"thumbnail":"/src/assets/insharior/row4.png","images":['/src/assets/insharior/row4.png']}}),_c('lightbox',{staticClass:"section__image no-radius",attrs:{"thumbnail":"/src/assets/insharior/row5.png","images":['/src/assets/insharior/row5.png']}})],1),_vm._m(8),_vm._m(9)])])}
